@import "src/styles/colors.module";

.Timeline {

  &Event {
    display: grid;
    gap: 10px;
    grid-template-columns: 200px minmax(600px, 0.5fr);
    grid-template-rows: max-content 0 1fr max-content;

    &:hover {
      background-color: #FAFAFA;

      & .TimelineEventSideLinePointTimestamp {
        color: $red-color;
      }

      & .TimelineEventSideLinePointCircle {
        border: 4px solid $red-color;
      }

      & .TimelineEventTitle {
        color: $red-color;
      }

      & .TimelineEventContentFade {
        background: linear-gradient(180deg, rgba(250,250,250,0) 0%, rgba(250,250,250,0) 20%, rgba(250,250,250,1) 100%);
      }

    }

    &Side {
      display: flex;
      justify-content: flex-end;
      margin-right: 50px;
      align-items: center;
      grid-row: 1/5;

      &Line {
        background-color: $light-gray-color;
        width: 6px;
        height: 100%;
        display: flex;
        flex-direction: column;

        &Point {
          display: grid;
          grid-template-columns: 1fr 20px;
          align-items: center;
          position: relative;
          width: 150px;
          left: -137px;
          margin-top: 15px;

          &Timestamp {
            background-color: #F0F0F0;
            font-style: normal;
            font-weight: 500;
            font-size: 17px;
            line-height: 30px;
            margin-right: 10px;
            color: $dark-gray-color;
            text-align: center;
            border-radius: 20px;
          }

          &Circle {
            background-color: $white-color;
            border: 4px solid $light-gray-color;
            box-sizing: border-box;
            height: 20px;
            width: 20px;
            border-radius: 20px;
          }
        }
      }
    }

    &Title {
      margin-top: 10px;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 42px;

    }

    &ContentFade {
      position: relative;
      cursor: pointer;
      height: 120px;
      background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0) 20%, rgba(255,255,255,1) 100%);
    }

    &Content {
      font-style: normal;
      font-weight: 400;
      font-size: 19px;
      line-height: 30px;
      padding-bottom: 10px;
      min-height: 100px;

      &Collapsed {
        max-height: 100px;
        overflow: hidden;
      }

    }

    &Tags {
      margin-bottom: 30px;
      display: flex;
      gap: 5px;
      flex-wrap: wrap;

      &Tag {
        border: 2px solid #F0F0F0;
        padding: 0 20px;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 30px;
        margin-right: 10px;
        color: $dark-gray-color;
        text-align: center;
        border-radius: 18px;
      }
    }

  }

}
