@import "src/styles/colors.module";

.TeamPage {


  & table {
    width: 820px;
    border-collapse: collapse;


    //border: 0.5px solid #868D99;
  }

  & tr {
    height: 60px;
    border-bottom: 1px dotted $light-gray-color;

    &:nth-of-type(1) {
      border-top: 1px dotted $light-gray-color;
    }

    &:hover {
      background-color: #FAFAFA;

      & td:nth-of-type(1) {
        color: $red-color;
      }
    }
  }

  & td {
    padding-left: 50px;
    font-style: normal;
    font-weight: 400;
    font-size: 19px;
    line-height: 30px;
    margin-bottom: 10px;
    min-height: 100px;

    &:nth-of-type(1) {
      font-weight: 500;
    }
  }

}
